*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

:is(::-webkit-inner-spin-button, ::-webkit-outer-spin-button) {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Regular), local(Roboto-Regular), url("Roboto-Regular.63f26da3.woff2") format("woff2"), url("Roboto-Regular.bae5207f.woff") format("woff"), url("Roboto-Regular.ca197847.ttf") format("truetype"), url("Roboto-Regular.98995063.eot") format("eot");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Medium), local(Roboto-Medium), url("Roboto-Medium.3f55cad1.woff2") format("woff2"), url("Roboto-Medium.cf46092f.woff") format("woff"), url("Roboto-Medium.91322885.ttf") format("truetype"), url("Roboto-Medium.31d14810.eot") format("eot");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Bold), local(Roboto-Bold), url("Roboto-Bold.f642050e.woff2") format("woff2"), url("Roboto-Bold.f7757558.woff") format("woff"), url("Roboto-Bold.fdb9b54a.ttf") format("truetype"), url("Roboto-Bold.1ac70682.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}

:root {
  --color-linear-one: linear-gradient(to bottom, #ffc226 0%, #f84119f8 100%);
  --color-orange: #f87719;
  --color-linear-two: linear-gradient(to bottom, #0000 63.48%, #000000e6 92.16%);
  --color-grey: #b7b7b7;
  --color-white: #fff;
  --color-linear-three: linear-gradient(to bottom, #f84119f8 0%, #f89f19f8 100%);
  --color-background: #111;
  --color-secondary-text: #f8f8f8;
  --color-secondary-black: #282828;
  --color-grey-white-theme: #595959;
}

.effect-style-shadow-one {
  box-shadow: 1px 1px 24px 2px #01010173;
}

.effect-style-shadow-two {
  box-shadow: 1px 1px 14px 4px #ff6b086b;
}

.effect-style-white-theme {
  box-shadow: 1px 1px 14px 4px #00000038;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--color-grey);
  background-color: #000;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.container {
  width: 100%;
  min-width: 200px;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.checkbox {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.star-outline, .star-half, .star, .slider, .toggle-switch:after, .toggle-switch:before {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.toggle-switch {
  opacity: 0;
  background-image: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 194px;
  width: 60px;
  height: 24px;
  display: inline-block;
  position: relative;
}

@media screen and (max-width: 768px) {
  .toggle-switch {
    width: 44px;
    height: 18px;
  }
}

.toggle-switch:before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.02604 7.77441C1.26766 11.1211 4.20061 13.8439 7.71076 13.9933C10.1873 14.0973 12.4021 12.9796 13.731 11.2185C14.2814 10.4972 13.9861 10.0163 13.0666 10.1788C12.6169 10.2568 12.1538 10.2893 11.6706 10.2698C8.38862 10.1398 5.704 7.48199 5.69058 4.34329C5.68386 3.49851 5.86508 2.69921 6.19394 1.9714C6.55637 1.16561 6.12012 0.782204 5.28117 1.12662C2.62339 2.21184 0.80456 4.80467 1.02604 7.77441Z' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
  width: 15px;
  height: 15px;
  position: absolute;
  top: 4px;
  left: 4px;
}

@media screen and (max-width: 768px) {
  .toggle-switch:before {
    width: 10px;
    height: 10px;
  }
}

.toggle-switch:after {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 13C10.7614 13 13 10.7614 13 8C13 5.23858 10.7614 3 8 3C5.23858 3 3 5.23858 3 8C3 10.7614 5.23858 13 8 13Z' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12.907 3.093L12.998 3.002M3.002 12.998L3.093 12.907M8 1.056V1M8 15V14.944M1.056 8H1M15 8H14.944M3.093 3.093L3.002 3.002M12.998 12.998L12.907 12.907' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  width: 16px;
  height: 16px;
  position: absolute;
  top: 4px;
  right: 4px;
}

@media screen and (max-width: 768px) {
  .toggle-switch:after {
    width: 10px;
    height: 10px;
  }
}

.toggle-switch .checkbox-label {
  z-index: 11;
  width: 100%;
  height: 100%;
  display: inline-block;
  position: relative;
}

.toggle-switch .checkbox-label:hover {
  cursor: pointer;
}

.slider {
  z-index: 10;
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.3518 2.64817C11.8304 -0.873235 6.13195 -0.884089 2.62393 2.62393C-0.884087 6.13195 -0.873233 11.8304 2.64817 15.3518C6.16958 18.8732 11.8681 18.8841 15.3761 15.3761C18.8841 11.8681 18.8732 6.16958 15.3518 2.64817Z' fill='white'/%3E%3Cpath d='M3.82352 3.57422C0.944573 6.65093 0.955378 11.6508 3.84766 14.7418' stroke='url(%23paint0_linear_261_31109)' stroke-width='1.2' stroke-linecap='round'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_261_31109' x1='3.58183' y1='4.37218' x2='-0.224667' y2='5.2955' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23FFC226'/%3E%3Cstop offset='1' stop-color='%23F84119'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E");
  width: 18px;
  height: 18px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 3px;
  left: 3px;
}

@media screen and (max-width: 768px) {
  .slider {
    width: 14px;
    height: 14px;
    top: 2px;
    left: 2px;
  }
}

.checkbox:checked + .slider {
  transform: translateX(36px)rotate(180deg);
}

@media screen and (max-width: 768px) {
  .checkbox:checked + .slider {
    transform: translateX(26px)rotate(180deg);
  }
}

.js-light-theme {
  background-color: var(--color-white);
}

.header-container {
  height: 48px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-list {
  font-weight: 500;
}

.js-light-theme .header-nav-list {
  color: var(--color-grey-white-theme);
}

.header-nav-list, .logo-title {
  display: none;
}

.header-mobile-open-menu {
  text-transform: uppercase;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.js-light-theme .header-mobile-open-menu {
  color: var(--color-grey-white-theme);
}

.mobile-menu-backdrop {
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: #fff3;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.25, .8, .25, 1), visibility .25s cubic-bezier(.25, .8, .25, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.js-light-theme .mobile-menu-backdrop {
  background-color: #0003;
}

.mobile-menu-backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.mobile-menu {
  z-index: 2000;
  color: #fff;
  background-color: #171717;
  border-radius: 0 8px 8px 0;
  width: 204px;
  height: 100vh;
  padding-top: 8px;
  padding-left: 20px;
  transition: transform .25s cubic-bezier(.25, .8, .25, 1) .25s;
  position: static;
  transform: translateX(-100%);
}

.js-light-theme .mobile-menu {
  background-color: var(--color-secondary-text);
}

.mobile-menu-backdrop.is-open .mobile-menu {
  transform: translateX(0);
}

.mobile-menu-logo {
  margin-bottom: 40px;
}

.mobile-menu-list {
  text-transform: uppercase;
  flex-direction: column;
  gap: 24px;
  font-size: 14px;
  line-height: 1.14;
  display: flex;
}

.js-light-theme .mobile-menu-list {
  color: var(--color-background);
}

.current {
  color: var(--color-orange);
}

.no-scroll {
  overflow: hidden;
}

.tablet-logo {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-container {
    height: 60px;
    padding-top: 6px;
    padding-bottom: 6px;
  }

  .tablet-logo {
    width: 48px;
    height: 48px;
    margin-right: 8px;
    display: inline-block;
  }

  .mobile-logo, .header-mobile-open-menu {
    display: none;
  }

  .header-nav-list {
    text-transform: uppercase;
    align-items: center;
    gap: 32px;
    display: flex;
  }

  .header-nav-list-item {
    transition: transform .25s cubic-bezier(.25, .8, .25, 1);
  }

  .header-nav-list-item:hover, .header-nav-list-item:focus {
    transform: scale(1.1);
  }

  .header-mobile-logo {
    align-items: center;
    gap: 8px;
    display: flex;
  }

  .js-light-theme .logo-title {
    color: var(--color-secondary-black);
  }

  .logo-title {
    color: var(--color-white);
    font-size: 24px;
    line-height: 1.16;
    display: block;
  }

  .header-nav-list {
    gap: 40px;
  }
}

